import { Component, OnInit, ViewChild } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { UtilsService } from 'src/app/services/utils.service';
import { AppValueService } from 'src/app/services/app-value.service';
import { TranslateService } from '@ngx-translate/core';
import { ModalEditOrderComponent } from 'src/app/components/modal-edit-order/modal-edit-order.component';
import { OrderManagementService } from './order-management.service';

@Component({
  selector: 'app-order-management',
  templateUrl: './order-management.page.html',
  styleUrls: ['./order-management.page.scss'],
})
export class OrderManagementPage implements OnInit {
  @ViewChild('contentSearch') contentSearch: any;  
  heightTable = "775";  
  constructor(private popoverController: PopoverController,
    public orderService: OrderManagementService,
    public utilsService: UtilsService,
    public appValueService: AppValueService,
    private translate: TranslateService) { }

  ngOnInit() {
  }
  showDetail(data: any, mode: any) {
    if(mode == "edit"){
      this.showModalEditOrder(data.ev, data.row._row.data);
    }
    else {
      this.showModalEditOrder(null, null);
    }    
  }

  async showModalEditOrder(ev: any, row: any) {
    const popover = await this.popoverController.create({
      component: ModalEditOrderComponent,
      id: "editOrderModal",
      event: ev,
      translucent: true,
      backdropDismiss: false,
      componentProps: { rowProduct: row },
      cssClass: "edit-order-popover"
    });
    return await popover.present();
  }
  ionViewDidEnter() {
    this.heightTable = this.contentSearch.el.offsetHeight;
  }
}
